<template>
  <div class="skyler card border-0 shadow-sm mb-4">
    <div
      v-if="name"
      class="card-header text-center"
      style="background: white !important;"
    >
      <h1 class="h3">{{ name }}</h1>
    </div>
    <img
      v-if="cenefa"
      src="https://res.cloudinary.com/dbszizqh4/image/upload/v1562472720/default.jpg"
      alt="imagen"
      class="img-skyler"
    />
    <Map v-if="geoLat && GeoLon" :lat="geoLat" :lon="geoLon" hidden />
    <div class="card-footer border-0 bg-white" v-if="slug">
      <div class="row">
        <div class="col-12 col-sm-7">
          <div class="media my-3">
            <img
              :src="
                image
                  ? image
                  : `https://res.cloudinary.com/dbszizqh4/image/upload/v1562473096/logo_default.png`
              "
              class="mr-3 rounded-circle img-avatar"
              alt="avatar"
            />
            <div class="media-body" v-if="slug">
              <h5 class="mt-0">@{{ slug }}</h5>
              <span v-if="description">{{ description }}</span>
            </div>
          </div>
        </div>
        <div class="col-12 col-sm-5 text-center">
          <div class="my-3">
            <i class="fas fa-star text-warning"></i>
            <i class="fas fa-star text-warning"></i>
            <i class="fas fa-star text-warning"></i>
            <i class="fas fa-star-half-alt text-warning"></i>
            <i class="far fa-star text-warning"></i>
          </div>
          <div class="mb-3" v-if="userGuest != userPage">
            <button type="button" class="btn btn-success btn-sm" hidden>
              <i class="far fa-check-circle"></i> Seguir
            </button>
            <button type="button" class="btn btn-outline-success btn-sm">
              <i class="far fa-times-circle"></i> Siguiendo
            </button>
            <button type="button" class="btn btn-primary btn-sm ml-2">
              <i class="far fa-envelope"></i> Mensaje
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Map from "@/components/Map";

export default {
  name: "Skyler",
  components: {
    Map
  },
  props: {
    id: String,
    cenefa: String,
    description: String,
    geoLat: String,
    geoLon: String,
    image: String,
    name: String,
    slug: String,
    userGuest: String,
    userPage: String
  }
};
</script>

<style scoped>
.img-skyler {
  height: 20rem;
  object-fit: cover;
  position: center center;
}

.img-avatar {
  width: 4rem;
  height: 4rem;
  object-fit: cover;
  position: center center;
}
</style>
